import { Injectable, OnDestroy, OnInit } from '@angular/core';
import { ChildActivationStart } from '@angular/router';
import { BehaviorSubject, of, ReplaySubject } from 'rxjs';

// tslint:disable-next-line: no-any
declare var webkitSpeechRecognition: any;

@Injectable({
  providedIn: 'root'
})
export class SpeechService implements OnInit, OnDestroy {


  // subscribable events
  welcome: ReplaySubject<string> = new ReplaySubject();
  begin: ReplaySubject<string> = new ReplaySubject();
  exercise: ReplaySubject<string> = new ReplaySubject();
  feedback: ReplaySubject<string> = new ReplaySubject();
  result: ReplaySubject<string> = new ReplaySubject();

  // intervals
  resultInterval = setInterval(() => {
    if(!this.processed) {
      if (Date.now() - this.timestamp >= 5000) {
        this.wordConcat();
        console.log(this.text);
        this.processed = true;
      }
    }

  }, 7500);

  lang = 'de-DE';
  recognition = new webkitSpeechRecognition();
  isListening = false;
  processed = true;
  timestamp = Date.now();

  public text = '';
  private tempWords = '';


  dict: { [id: string]: { [id: string]: (string | string[]) } } = {
    'en-US': { welcome: 'welcome', begin: 'begin', exercise: ['push up', 'military press', 'dead lift'] },
    'de-DE': { welcome: 'willkommen', begin: 'begin', exercise: ['liegestütze','liege stütze', 'militär presse', 'kreuz heben', 'kreuzheben'] }
  };
  reason: boolean = false;

  ngOnDestroy(): void {
    if (this.resultInterval) {
      clearInterval(this.resultInterval);
    }
  }


  constructor() {
    this.init();
  }

  ngOnInit(): void {

  }

  public init(): void {
    this.recognition.interimResults = true;
    this.recognition.lang = this.lang;
    this.recognition.continuous = true;

    this.recognition.addEventListener('result', (speechEvent: any) => {
      this.processed = false;
      this.timestamp = Date.now();
      console.log(speechEvent);

      const transcript = Array.from(speechEvent.results)
        .map((result: any) => result[0])
        .map((result) => {
          console.log(result.transcript);
          return result.transcript;
        }).join('');

      const lt = transcript.toLowerCase();
      if(lt.includes("ende") && lt.includes("sprachsteuerung")) {
        console.log('BEENDE');
        this.reason = true;
        this.stop();
      }



      if (lt.includes(this.dict[this.lang].welcome as string) || (lt.includes('will') && lt.includes('kommen'))) {
        this.welcome.next(transcript);
      }
      if (lt.includes(this.dict[this.lang].begin as string)) {
        this.begin.next(transcript);
      }
      if ((this.dict[this.lang].exercise as string[]).some(val => { lt.includes(val) })) {
        console.log("exercise known");
        this.exercise.next(transcript);
      } else if (lt.includes('übung') || (lt.includes('übe'))) {
        this.exercise.next(transcript);
      } else {
        this.result.next(lt);
      }
      this.tempWords = transcript;
    });

    this.recognition.addEventListener('end', () => {
      if(this.reason) {
        console.log('End speech recognition');
      } else {
        console.log('restarting speech recognition');
        this.start();
      }
    });
  }

  public start(): void {
    this.isListening = true;
    this.recognition.start();
    console.log('Speech recognition started');
  }

  public stop(): void {
    this.isListening = false;
    this.wordConcat();
    this.recognition.stop();
  }

  private wordConcat(): void {
    this.text = this.text + ' ' + this.tempWords + '.';
    this.tempWords = '';
  }


}
