import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatTabGroup } from '@angular/material/tabs';
import { SpeechService } from '../services/speech.service';
import {Howl, Howler} from 'howler';

@Component({
  selector: 'app-step',
  templateUrl: './step.component.html',
  styleUrls: ['./step.component.scss']
})
export class StepComponent implements OnInit, AfterViewInit {

  welcome = "";
  begin = "";
  exercise = "";
  result = "";
  zoomSound = new Howl({
    src: ['../../assets/zoom.mp3']
  });
  AudioContext = window.AudioContext;



  active = "exercise";
  @ViewChild('tabs', { read: ElementRef }) tab!: ElementRef;


  constructor(private speechService: SpeechService) { }

  ngOnInit(): void {
    //this.speechService.start();
  }

  ngAfterViewInit(): void {
    this.speechService.result.subscribe(val => {
      this.result = val;
    });
    this.speechService.welcome.subscribe(val => {
      this.active = 'intro';
      //this.playZoomSound();
    });
    this.speechService.exercise.subscribe(() => {
      this.active = 'exercise';
    });
    this.speechService.begin.subscribe(val => {
      this.begin = val;
    });
  }

  playZoomSound(): void {
    this.zoomSound.play();
  }








}
